<template>
    <div class="loginOne">
        <el-card class="box-card">
            <div slot="header" class="clearfix">
                <span>Vue项目学习之登录页面</span>
            </div>
            <el-form ref="form" :model="form" label-width="80px">
                <el-form-item prop="username" label="用户名" :rules="[
                    { required: true, message: '请输入用户名', trigger: 'blur' },
                    { min: 6, max: 12, message: '长度在6-12位字符', trigger: 'blur' },
                ]">
                    <el-input v-model="form.username"></el-input>
                </el-form-item>
                <el-form-item prop="password" label="密码" :rules="[
                    { required: true, message: '请输入密码', trigger: 'blur' },
                    { min: 6, max: 12, message: '长度在6-12位字符', trigger: 'blur' },
                ]">
                    <el-input v-model="form.password" type="password"></el-input>
                </el-form-item>
                <el-button type="primary" @click="login('form')">登录</el-button>
            </el-form>
        </el-card>
    </div>
</template>

<script>
export default {
    data() {
        return {
            form: {
                username: '',
                password: ''
            }
        }
    },
    methods: {
        login(form) {
            this.$refs[form].validate((valid) => {
                if (valid) {
                    console.log(this.form)
                    this.$message({ message: '登录成功', type: 'success' })
                    this.$router.push('/home')
                    // this.axios.post('http://1.116.64.64:5004/api2/login',this.form)
                    // .then(res=>{
                    //     console.log(res)
                    //     if(res.status == 200){
                    //         localStorage.setItem('username',res.data.username)
                    //         this.$message({message:'登录成功',type:'success'})
                    //         this.$router.push('/home')
                    //     }
                    // })
                    // .catch(err=>{
                    //     console.error(err)
                    // })

                } else {
                    console.error(this.form)
                }
            })
        }
    }
}
</script>
 
<style lang="less">
.loginOne {
    width: 100%;
    height: 100%;
    background-color: cornflowerblue;
    position: absolute;

    .box-card {
        width: 400px;
        height: 300px;
        // position: absolute;
        margin: 100px auto;

        .el-card__header {
            font-size: 1.5rem;
            text-align: center;
        }

        .el-form {
            .el-button {
                width: 100%;
            }
        }
    }
}
</style>